@tailwind base;
@tailwind components;
@tailwind utilities;

::-webkit-scrollbar {
    width: 8px;
  }
  
::-webkit-scrollbar-track {
  background: #f1f1f1;
}
  
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 4px;
}
  
::-webkit-scrollbar-thumb:hover {
  background: #686767;
}
  
.swal2-html-container.custom-swal-font {
  font-family: 'Indie Flower', sans-serif; 
}